	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is usability testing?</h2>\n<p>Usability testing is the process of evaluating a product or service by testing it with users and observing their behavior. These tests can be performed with individuals or in a group and focus on user behavior. Participants will try to complete set tasks while observers watch, listen, and take notes. The results from the test are then used to make improvements to the product.</p>\n<h2>When should I conduct a usability test?</h2>\n<p>Usability testing should be done iteratively throughout a project, starting with wireframes or prototypes. design mockups can also be used for testing.</p>\n<h2>Why should I perform a usability test?</h2>\n<ol>\n<li>usability tests help you identify problems early in the project when they’re much easier to  x.</li>\n<li>Collect data.</li>\n<li>determine the participant’s satisfaction with the product.</li>\n</ol>\n<h2>How to conduct a usability test</h2>\n<p>Identify your target audience. If you’re testing an existing site, you can leverage analytics to do this. Write a test plan including a script and the interactions/features to be tested, create and prioritize tasks that the user can complete during test – aim for 5-10 tasks for a 60 minute test. Make the tasks realistic, actionable, and avoid giving clues to the answers. Ideally a usability test should include a facilitator and a note taker. If a note taker is unavailable, the facilitator can take notes but they likely won’t be as thorough.</p>\n<p>Facilitator reads a participant one task at a time and allows the participant to complete the task without intervention until all tasks are complete. be sure to take notes on the questions that are being asked. ask the participant to talk through their task aloud to better understand their decision-making. Take note of the time it takes a user to complete each task. Take notes on what they say, areas they struggle in, and places where content may be unclear. We recommend testing with 3-5 participants per round of testing. Test one participant per session.</p>\n<p><strong>Task Writing Tips:</strong></p>\n<ul>\n<li>Make the task realistic</li>\n<li>Make the task actionable</li>\n<li>Avoid giving clues and describing the steps</li>\n</ul>\n<p><strong>Test moderating tips:</strong></p>\n<ul>\n<li>Don’t lead the participant</li>\n<li>Don’t interrupt at the wrong time</li>\n<li>Follow a script/checklist to prevent bias</li>\n<li>Observe and listen</li>\n</ul>","frontmatter":{"title":"Usability Testing","overview":[{"heading":"Investment","amount":5,"description":"Usability testing involves a heavy investment compared to other user research methods. first, you need something to test -- prototypes, wireframes, designs, etc. -- which require e ort and research to create themselves. You also need to recruit participants and compensate them in some way. finally, the results of the test need to be documented in a way that is useful for the design team."},{"heading":"Validity","amount":5,"description":"The results from usability testing are nearly irrefutable. When you ask a real user to use a real product and notice a trend that an element of that product is problematic, then that element is almost certainly problematic."},{"heading":"Expertise","amount":5,"description":"There are many resources available to assist with facilitation of usability testing, and doing it right does require thoughtful pre-work. a key pitfall to be aware of is the potential for a facilitator to prime a participant, which can potentially invalidate results. Usability tests greatly bene t from a skilled hand."}],"resources":[{"text":"Rocket Surgery Made Easy by Steve Krug","url":"http://www.sensible.com/rsme.html"},{"text":"Nielsen Norman Group – Why You Only Need to Test with Five users","url":"https://www.nngroup.com/articles/why-you-only-need-to-test-with-5-users/"},{"text":"Blink UX - Sample Size Calculator","url":"https://blinkux.com/usability-sample-size/"},{"text":"Demystifying Usability - Latest Research","url":"https://cdi.edu.au/blog2/files/business-model.jpg"},{"text":"UX Matters - Usability Testing Myths and Misconceptions","url":"https://www.uxmatters.com/mt/archives/2015/01/17-usability-testing-myths-and-misconceptions.php"},{"text":"UX Sisters - Usability Vs Beta Testing","url":"http://www.uxsisters.com/2014/08/usability-vs-beta-testing"}],"relatedMethods":[{"text":"A/B Testing","url":"/ab-test/"}]},"fields":{"slug":"/usability-testing/"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"Empathy Map","url":"/empathy-map/"}}},{"node":{"frontmatter":{"title":"Focus Group","url":"/focus-group/"}}},{"node":{"frontmatter":{"title":"Journey Map","url":"/journey-map/"}}},{"node":{"frontmatter":{"title":"Participatory Design","url":"/participatory-design/"}}},{"node":{"frontmatter":{"title":"Usability Testing","url":"/usability-testing/"}}},{"node":{"frontmatter":{"title":"Card Sort","url":"/card-sort/"}}},{"node":{"frontmatter":{"title":"A/B Test","url":"/ab-test/"}}},{"node":{"frontmatter":{"title":"Affinity Diagram","url":"/affinity-diagram/"}}},{"node":{"frontmatter":{"title":"Interview","url":"/interview/"}}},{"node":{"frontmatter":{"title":"User Research Guidelines","url":null}}},{"node":{"frontmatter":{"title":"Persona","url":"/persona/"}}}]}},"pathContext":{"slug":"/usability-testing/"}}

